.gallery {
  width: calc(100px + (100–950) * ((100vw — 300px) / (1920–300) ) );
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 30px;
  margin: 0;
  display: flex;
}

.photo-card {
  width: 300px;
  cursor: pointer;
  border-radius: 10px;
  flex-wrap: wrap;
  margin: 15px 15px 0;
  display: flex;
  box-shadow: 0 3px 8px rgba(0, 0, 0, .5);
}

.photo-card:hover {
  transition: all .7s;
  overflow: hidden;
  transform: scale(1.2);
}

.search-form {
  z-index: 2;
  opacity: .4;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 25px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.btn-submit {
  color: #000;
  cursor: pointer;
  background-color: #079ea3;
  border-radius: 10px;
  margin-left: 10px;
}

.info {
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  display: flex;
}

p {
  flex-wrap: wrap;
  display: flex;
}

b {
  margin-right: 10px;
}

b:last-child {
  margin-right: 0;
}

img {
  max-width: 100%;
  height: auto;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: block;
}

.load-more {
  text-align: center;
  width: 120px;
  height: 25px;
  color: #fff;
  cursor: pointer;
  background-color: #00f;
  border-radius: 10px;
  margin: 40px auto;
  display: block;
}

.is-hidden {
  display: none;
}

.load-more:hover, .load-more:focus {
  color: #fff;
  background-color: #09044a;
}

